import React, { useState, useRef, useEffect, useLayoutEffect } from 'react';
import Quiz from './component/Quiz';
import { GlobalStyle, ANI_TIME, MAX_WIDTH, BACKGROUND_COLOR } from './common';
import { ElementType, IQuiz, IThink } from './type';
import styled from 'styled-components';
import { playStarAnimation, setStarPosition } from './animation';
import Modal from './component/Modal';
import Think from './component/Think';
import useWindowSize from './useWindowSize';
import Credit from './component/Credit';
import Header from './component/Header';

function App() {
  const [quizIndex, setQuizIndex] = useState(0);
  const [star, setStar] = useState(0);
  const [thinkItem, setThinkItem] = useState<IThink | null>(null);
  const [showNext, setShowNext] = useState(false);
  const [faceIn, setFaceIn] = useState(true);
  const [showCredit, setShowCredit] = useState(false);
  const [failCount, setFailCount] = useState(0);
  const starRef = useRef<HTMLElement>(null);
  const windowSize = useWindowSize();
  function onFail() {
    setFailCount(failCount + 1);
    setQuizIndex(0);
    setStar(0);
    window.scrollTo(0, 0);
  }
  function onNext() {
    setFaceIn(false);
    setShowNext(true);
    setTimeout(() => {
      setFaceIn(true);
    }, 1);
    setTimeout(() => {
      setShowNext(false);
      setQuizIndex(quizIndex + 1);
    }, ANI_TIME);
    const item = THINK_LIST.find(item => item.quizIndex === quizIndex + 1);
    if (item) {
      window.scrollTo(0, 0);
      setThinkItem(item);
      if (quizIndex + 1 === MAX_QUIZ_INDEX) {
        setShowCredit(true);
        setStarPosition({ x: -100, y: -100 });
      }
    }
  }
  function setStarPositionFromEl() {
    if (starRef.current) {
      const bound = starRef.current.getBoundingClientRect();
      const x = bound.left + bound.width / 2 - 40;
      const y = bound.top + bound.height / 2 - 1;
      setStarPosition({ x, y });
    }
  }
  function increaseStar(justAni?: boolean) {
    setStarPositionFromEl();
    playStarAnimation();
    if (!justAni) {
      setStar(star + 1);
    }
  }
  useEffect(() => {
    increaseStar(true);
  }, []);
  useEffect(() => {
    setStarPositionFromEl();
  }, [windowSize.width]);
  function onCloseModal() {
    setThinkItem(null);
    window.scrollTo(0, 0);
  }
  function onStartOver() {
    setShowCredit(false);
    setQuizIndex(0);
    setStar(0);
  }
  useLayoutEffect(() => {
    setStarPositionFromEl();
  }, [showCredit]);
  return (
    <>
      {!showCredit && (
        <Header
          quizIndex={quizIndex}
          star={star}
          starRef={starRef}
          height={HEADER_HEIGHT}
        />
      )}
      {!showCredit && (
        <Container key={failCount}>
          {QUIZ_LIST.map(
            (data, index) =>
              ((quizIndex === index - 1 && showNext) ||
                quizIndex === index) && (
                <Quiz
                  key={index}
                  show={
                    (quizIndex === index - 1 && faceIn) || quizIndex === index
                  }
                  onFail={onFail}
                  onNext={onNext}
                  increaseStar={increaseStar}
                  data={data}
                />
              ),
          )}
        </Container>
      )}
      {showCredit && <Credit onStartOver={onStartOver} />}
      <GlobalStyle />
      <Modal
        isOpen={!!thinkItem}
        onRequestClose={onCloseModal}
        contentStyle={{
          margin: '10vh auto',
          height: '80vh',
          maxWidth: `${MAX_WIDTH - 60}px`,
          top: 0,
          right: '5vw',
          left: '5vw',
          bottom: undefined,
          boxSizing: 'border-box',
          textAlign: 'center',
        }}
      >
        <Think data={thinkItem} onClose={onCloseModal} />
      </Modal>
    </>
  );
}

