// @ts-ignore
import mojs from 'mo-js';
import { ZIndex } from './common';

function onStart(this: any) {
  this.el.style.display = 'block';
  this.el.style.zIndex = `${ZIndex.Header + 5}`;
}
function onComplete(this: any) {
  this.el.style.display = 'none';
}

export const playPassAnimation = (function() {
  const burst = new mojs.Burst({
    left: 0,
    top: 0,
    count: 8,
    radius: { 50: 150 },
    children: {
      shape: 'line',
      stroke: ['white', '#FFE217', '#FC46AD', '#D0D202', '#B8E986', '#D0D202'],
      scale: 1,
      scaleX: { 1: 0 },
      degreeShift: 'rand(-90, 90)',
      radius: 'rand(20, 40)',
      delay: 'rand(0, 150)',
      isForce3d: true,
    },
    onStart,
    onComplete,
  });

  const largeBurst = new mojs.Burst({
    left: 0,
    top: 0,
    count: 4,
    angle: 45,
    radius: { 0: 450 },
    children: {
      shape: 'line',
      stroke: '#4ACAD9',
      scale: 1,
      scaleX: { 1: 0 },
      radius: 100,
      duration: 450,
      isForce3d: true,
      easing: 'cubic.inout',
    },
    onStart,
    onComplete,
  });

  const CIRCLE_OPTS = {
    left: 0,
    top: 0,
    scale: { 0: 1 },
  };

  const largeCircle = new mojs.Shape({
    ...CIRCLE_OPTS,
    fill: 'none',
    stroke: 'white',
    strokeWidth: 4,
    opacity: { 0.25: 0 },
    radius: 250,
    duration: 600,
    onStart,
    onComplete,
  });

  const smallCircle = new mojs.Shape({
    ...CIRCLE_OPTS,
    fill: 'white',
    opacity: { 0.5: 0 },
    radius: 30,
    onStart,
    onComplete,
  });

  return function(e: any) {
    burst
      .tune({ x: e.pageX, y: e.pageY })
      .generate()
      .replay();

    largeBurst.tune({ x: e.pageX, y: e.pageY }).replay();
    largeCircle.tune({ x: e.pageX, y: e.pageY }).replay();
    smallCircle.tune({ x: e.pageX, y: e.pageY }).replay();
  };
})();

export const playFailAnimation = (function() {
  const COLORS = {
    RED: '#FD5061',
    YELLOW: '#FFCEA5',
    BLACK: '#29363B',
    WHITE: 'white',
    VINOUS: '#A50710',
  };

  const bgBurst = new mojs.Burst({
    left: 0,
    top: 0,
    count: 3,
    radius: 0,
    degree: 0,
    isShowEnd: false,
    children: {
      fill: [COLORS.RED, COLORS.WHITE, COLORS.BLACK],
      radius: 'stagger(200, 2)',
      scale: { 0.25: 3 },
      duration: 500,
      delay: 'stagger(50)',
      easing: ['cubic.out', 'cubic.out', 'cubic.out'],
      isForce3d: true,
    },
    onStart,
    onComplete,
  });

  const burst1 = new mojs.Burst({
    left: 0,
    top: 0,
    count: 5,
    radius: { 50: 250 },
    children: {
      fill: 'white',
      shape: 'line',
      stroke: [COLORS.WHITE, COLORS.VINOUS],
      strokeWidth: 12,
      radius: 'rand(30, 60)',
      radiusY: 0,
      scale: { 1: 0 },
      pathScale: 'rand(.5, 1)',
      degreeShift: 'rand(-360, 360)',
      isForce3d: true,
    },
    onStart,
    onComplete,
  });

  const burst2 = new mojs.Burst({
    top: 0,
    left: 0,
    count: 3,
    radius: { 0: 250 },
    children: {
      shape: ['circle', 'rect'],
      points: 5,
      fill: [COLORS.WHITE, COLORS.VINOUS],
      radius: 'rand(30, 60)',
      scale: { 1: 0 },
      pathScale: 'rand(.5, 1)',
      isForce3d: true,
    },
    onStart,
    onComplete,
  });

  const CIRCLE_OPTS = {
    left: 0,
    top: 0,
    fill: COLORS.WHITE,
    scale: { 0.2: 1 },
    opacity: { 1: 0 },
    isForce3d: true,
    isShowEnd: false,
  };

  const circle1 = new mojs.Shape({
    ...CIRCLE_OPTS,
    radius: 200,
    onStart,
    onComplete,
  });

  const circle2 = new mojs.Shape({
    ...CIRCLE_OPTS,
    radius: 240,
    easing: 'cubic.out',
    delay: 150,
    onStart,
    onComplete,
  });

  return function(e: any) {
    burst1
      .tune({ x: e.pageX, y: e.pageY })
      .generate()
      .replay();
    burst2
      .tune({ x: e.pageX, y: e.pageY })
      .generate()
      .replay();
    circle1.tune({ x: e.pageX, y: e.pageY }).replay();
    circle2.tune({ x: e.pageX, y: e.pageY }).replay();
    bgBurst.tune({ x: e.pageX, y: e.pageY }).replay();
  };
})();

const starFunctions = (function() {
  class Star extends mojs.CustomShape {
    getShape() {
      return '<path d="M5.51132201,34.7776271 L33.703781,32.8220808 L44.4592855,6.74813038 C45.4370587,4.30369752 47.7185293,3 50,3 C52.2814707,3 54.5629413,4.30369752 55.5407145,6.74813038 L66.296219,32.8220808 L94.488678,34.7776271 C99.7034681,35.1035515 101.984939,41.7850013 97.910884,45.2072073 L75.9109883,63.1330483 L82.5924381,90.3477341 C83.407249,94.4217888 80.4739296,97.6810326 77.0517236,97.6810326 C76.0739505,97.6810326 74.9332151,97.3551083 73.955442,96.7032595 L49.8370378,81.8737002 L26.044558,96.7032595 C25.0667849,97.3551083 23.9260495,97.6810326 22.9482764,97.6810326 C19.3631082,97.6810326 16.2668266,94.4217888 17.4075619,90.3477341 L23.9260495,63.2960105 L2.08911601,45.2072073 C-1.98493875,41.7850013 0.296531918,35.1035515 5.51132201,34.7776271 Z" />';
    }
  }
  mojs.addShape('star', Star);

  const RADIUS = 28;
  const circle = new mojs.Shape({
    left: 0,
    top: 0,
    stroke: '#FF9C00',
    strokeWidth: { [2 * RADIUS]: 0 },
    fill: 'none',
    scale: { 0: 1, easing: 'quad.out' },
    radius: RADIUS,
    duration: 450,
    onStart,
    onComplete,
  });

  const burst = new mojs.Burst({
    left: 0,
    top: 0,
    radius: { 6: RADIUS - 3 },
    angle: 45,
    children: {
      shape: 'star',
      radius: RADIUS / 2.2,
      fill: '#FD7932',
      degreeShift: 'stagger(0,-5)',
      duration: 700,
      delay: 200,
      easing: 'quad.out',
    },
    onStart,
    onComplete,
  });

  const star = new mojs.Shape({
    left: 0,
    top: 0,
    shape: 'star',
    fill: '#FF9C00',
    scale: { 0: 1 },
    easing: 'elastic.out',
    duration: 1600,
    delay: 300,
    radius: RADIUS / 2.35,
    onStart: function(this) {
      this.el.style.zIndex = `${ZIndex.Header + 1}`;
    },
  });

  const timeline = new mojs.Timeline({ speed: 1.5 });

  timeline.add(burst, circle, star);

  const setStarPosition = function(position: { x: number; y: number }) {
    const left = `${position.x}px`;
    const top = `${position.y}px`;
    burst.el.style.left = left;
    burst.el.style.top = top;
    circle.el.style.left = left;
    circle.el.style.top = top;
    star.el.style.left = left;
    star.el.style.top = top;
    burst.el.style.position = 'fixed';
    circle.el.style.position = 'fixed';
    star.el.style.position = 'fixed';
  };
  const playStarAnimation = function() {
    timeline.replay();
  };
  return {
    playStarAnimation,
    setStarPosition,
  };
})();
export const setStarPosition = starFunctions.setStarPosition;
export const playStarAnimation = starFunctions.playStarAnimation;
